<template>
  <div class="ue_case">
    <script
      id="editor"
      type="text/plain"
    ></script>
  </div>
</template>
<script>
import "../../../static/UE/ueditor.config";
import "../../../static/UE/ueditor.all";
import "../../../static/UE/ueditor.parse";
export default {
  name: "UE",
  data() {
    return {
      editor: null
    };
  },
  props: {
    defaultMsg: {
      type: String
    },
    config: {
      type: Object
    }
  },
  mounted() {
    const _this = this;
    this.editor = UE.getEditor("editor", this.config); // 初始化UE
    this.editor.addListener("ready", function() {
      _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
    });
  },
  methods: {
    getUEContent() {
      // 获取内容方法
      return this.editor.getContent();
    }
  },
  destroyed() {
    this.editor.destroy();
  }
};
</script>
<style lang="less">
.ue_case {
  .edui-default .edui-box {
    line-height: 16px;
  }
}
</style>



